* {
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  -khtml-user-select: none;
  max-width: 100%;
  margin: 0;
  padding: 0;
  overflow-x: clip;
}

body {
  height: 100%;
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

html {
  height: -webkit-fill-available;
}

.hide {
  visibility: hidden;
}

.skip-link {
  color: #fff;
  z-index: 10;
  background: #000;
  padding: 8px;
  position: absolute;
  top: -40px;
  left: 0;
}

.skip-link:focus {
  top: 0;
}

.close-button {
  width: 20px;
  height: 20px;
  color: #fff;
  cursor: pointer;
  background-color: #777;
  border: none;
  border-radius: 20px;
}

.hidden {
  display: none !important;
}

body {
  scroll-behavior: smooth;
  background-color: #000;
  font-family: Raleway, sans-serif;
}

.wrapper {
  scroll-snap-type: y mandatory;
  height: 100vh;
  justify-content: center;
  align-items: center;
  overflow-y: scroll;
}

.section {
  text-align: center;
  height: 100vh;
  scroll-snap-align: start;
  color: #fff;
  text-shadow: 0 2px #b2a98f, 0 4px 3px #00000026, 0 8px 1px #0000001a;
  justify-content: center;
  align-items: center;
  font-size: 5vw;
  display: flex;
}

.bg-home {
