.timeline {
  box-sizing: border-box;
  height: 100%;
  line-height: 1;
  position: relative;
}

.timeline *, .timeline :before, .timeline :after {
  box-sizing: inherit;
}

.timeline:not(.timeline--horizontal):before {
  content: "";
  width: 4px;
  z-index: 1;
  background-color: #ddd;
  margin-left: -2px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
}

.timeline__items {
  height: 100%;
  flex-direction: column;
  justify-content: space-evenly;
  display: flex;
}

.timeline__wrap {
  z-index: 2;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.timeline__item {
  width: 50%;
  z-index: 2;
  cursor: pointer;
  padding: 10px 40px 10px 0;
  font-size: 1rem;
  position: relative;
}

.timeline__item:after {
  content: "";
  height: 20px;
  width: 20px;
  z-index: 1;
  background-color: #fff;
  border: 4px solid #ddd;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  right: -10px;
  transform: translateY(-50%);
}

.timeline__selected:after {
  background-color: gold;
  border: 4px solid #a48a00;
}

.timeline__item.animated {
  opacity: 0;
  animation-duration: 1s;
  animation-fill-mode: both;
}

.timeline__item.fadeIn {
  animation-name: fadeIn;
}

.timeline__item--left {
  left: 0;
}

.timeline__item--right {
  padding: 10px 0 10px 40px;
  left: 50%;
}

.timeline__item--right:after {
  left: -10px;
}

.timeline__item--right .timeline__content:before {
  border: 10px solid #0000;
  border-left: none;
  border-right: 12px solid #ccc;
  left: -12px;
}

.timeline__item--right .timeline__content:after {
  border: 9px solid #0000;
  border-left: none;
  border-right: 11px solid #fff;
  left: -10px;
}

.timeline__content {
  color: #333;
  background-color: #fff;
  border: 2px solid #0000;
  border-radius: 15px;
  flex-direction: column;
  padding: 10px;
  font-size: 2em;
  display: flex;
  position: relative;
}

.timeline__active, .timeline__content:active {
  border: 2px solid gold;
}

.timeline__content::selection {
  border: 2px solid gold;
}

.timeline__content:hover {
  border: 2px solid gold;
  transition: border .2s ease-in;
}

.timeline__content:before, .timeline__content:after {
  content: "";
  height: 0;
  width: 0;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.timeline__content:before {
  z-index: 1;
  border-top: 10px solid #0000;
  border-bottom: 10px solid #0000;
  border-left: 12px solid #ccc;
  right: -12px;
}

.timeline__content:after {
  z-index: 2;
  border-top: 9px solid #0000;
  border-bottom: 9px solid #0000;
  border-left: 11px solid #fff;
  right: -10px;
}

.timeline__content h2 {
  margin: 0 0 10px;
  font-size: 20px;
  font-weight: 700;
}

.timeline__content p {
  margin-bottom: 10px;
  font-size: 15px;
  line-height: 1.5;
}

.timeline--mobile {
  padding: 0;
}

.timeline--mobile:before {
  margin: 0 !important;
  left: 10px !important;
}

.timeline--mobile .timeline__item {
  width: 100%;
  padding-left: 40px;
  left: 0;
}

.timeline--mobile .timeline__item:after {
  margin: 0;
  left: 2px;
}

.timeline--mobile .timeline__item .timeline__content:before {
  border: 12px solid #0000;
  border-left: none;
  border-right-color: #ccc;
  left: -12px;
}

.timeline--mobile .timeline__item .timeline__content:after {
  border: 10px solid #0000;
  border-left: none;
  border-right-color: #fff;
  left: -10px;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    top: 70px;
  }

  100% {
    opacity: 1;
    top: 0;
  }
}

@keyframes liftUp {
  0% {
    top: 0;
  }

  100% {
    top: -15px;
  }
}

